import React from 'react';
import { Terminal, ArrowRight } from 'lucide-react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import textureB from '../assets/double-bubble-dark.webp';
import abstractBrainHeart from '../assets/images/abstract-brain-heart.jpg';


const Home = () => (
  <div
  className="min-h-screen bg-black text-white"
  style={{
    backgroundImage: `url(${textureB})`,
    backgroundSize: 'auto',
    backgroundRepeat: 'repeat',
  }}
>
  <div className="min-h-screen flex flex-col items-center justify-center px-8 py-6 relative">
    {/* Centered Title */}
    <section className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
  <div className="text-center space-y-6" style={{ marginTop: '10rem' }}>
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="relative inline-block shadow-lg"
      style={{
        width: '200px',
        height: '200px',
        borderRadius: '50%',
        overflow: 'hidden',
        border: '4px solid rgba(255, 255, 255, 0.2)'
      }}
    >
      <img
        src={abstractBrainHeart} // Replace with the path to the uploaded image
        alt="Abstract Brain and Heart"
        className="object-cover w-full h-full"
        
      />
    </motion.div>
    
  </div>
</section>
    <div className="text-center space-y-6 max-w-3xl mt-0">
    

{/* Call-to-action */}

      <h1 className="text-lg font-bold py-18 text-green-400">
        Where is your freedom?
      </h1>

      <p className="text-lg text-orange-200" style={{fontFamily: 'monospace', marginTop: '32rem'}}>
        Insecurities and artificial limitations breed corruption.
      </p>

      <p className="text-lg text-blue-200" style={{fontFamily: 'monospace', marginTop: '36rem', marginBottom: '32rem'}}>
        Freedom, Clarity, and Connection thrive in their absence.
      </p>

      <div className="mt-10">
      <Link
        to="/projects"
        style={{fontFamily: 'monospace'}}
        className="inline-flex items-center px-4 py-2 my-2 text-lg font-semibold text-white bg-gradient-to-r from-red-500 via-blue-600 to-red-700 rounded-full shadow-lg hover:shadow-xl hover:from-red-600 hover:to-blue-800 transition-all duration-300"
        
      >
        <span style={{fontFamily: 'monospace'}}>Projects</span>
      </Link>
      <p className="text-1xl text-blue-400" style={{fontFamily: 'monospace', marginTop: '2rem', marginBottom: '3rem'}}>
        The moment you realize you are nothing, you have found salvation.
      </p>
      <p className="text-1xl text-blue-400" style={{fontFamily: 'monospace', marginTop: '2rem', marginBottom: '1rem'}}>
        You need no religion, nor anything else. 
      </p>
      <p className="text-1xl text-blue-400" style={{fontFamily: 'monospace', marginTop: '2rem', marginBottom: '1rem'}}>
        Be as you are. 
      </p>
      <p className="text-1xl text-blue-400" style={{fontFamily: 'monospace', marginTop: '2rem', marginBottom: '1rem'}}>
        There is nothing wrong with nothing.
      </p>
    </div>
    </div>

    
  </div>
</div>

);

export default Home;
