import React from 'react';
import { ExternalLink, Github } from 'lucide-react';
import PropTypes from 'prop-types';
import { motion } from 'framer-motion';
import textureB from '../assets/folk-pattern-black.webp';
import textureA from '../assets/connectwork.webp';
import cultures from '../assets/images/on-the-edge.webp';

const projectData = [
  {
    id: 1,
    icon: "",
    title: "Globethoughts",
    subtitle: "A platform for collective intelligence.",
    techStack: "Node.js, Express, MongoDB",
    valuation: "$5M - $10M by 2027",
    business: "A nonprofit funded by academic and private sponsorships, subscription-based services, user-taught ads for free content, and tips for user-curators.",
    category: "Community Groups",
    link: "https://www.globe-thoughts.com"
  },
  {
    id: 2,
    icon: "",
    title: "Memeplex",
    subtitle: "Relational Blogging Network",
    techStack: ["React", "Next.js", "PostgreSQL"],
    valuation: "$100K in 2 years",
    business: "Increases metadata through user engagement, building collective knowledge.",
    category: "Knowledge Enthusiasts",
    link: "https://www.memeplex.pro"
  },
  {
    id: 3,
    icon: "",
    title: "Apex",
    subtitle: "Creative Marketplace",
    techStack: ["Next.js", "Tailwind CSS", "Node.js"],
    valuation: "$500K in 2 years",
    business: "Starts with t-shirt design and expands to a full creative marketplace.",
    category: "Creative Entrepreneurs",
    link: "https://www.apex-vets.pro"
  },
  {
    id: 4,
    icon: "",
    title: "Nexus",
    subtitle: "Innovation Hub",
    techStack: ["Next.js", "Prisma", "React"],
    valuation: "$750K in 2 years",
    business: "Combines physical and digital spaces to foster innovation and support veterans.",
    category: "Veteran Innovators",
    link: "https://www.nexus-vets.pro"
  }
];

const ProjectCard = ({ project }) => (
  <div
    className="bg-black overflow-hidden transition-all duration-300 hover:bg-texture"
    style={{
      backgroundImage: `url(${textureA})`,
      backgroundSize: 'auto',
      backgroundRepeat: 'repeat',
      border: '18px solid rgba(44, 50, 221, 0.1)',
    }}
  >
    <div className="p-6 space-y-4">
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-4">
          <span className="text-4xl">{project.icon}</span>
          <div>
            <h3 className="text-xl font-bold text-white">{project.title}</h3>
            <p className="text-blue-400">{project.subtitle}</p>
          </div>
        </div>
        <a
          href={project.link}
          target="_blank"
          rel="noopener noreferrer"
          className="text-gray-400 hover:text-white"
        >
          <ExternalLink className="h-6 w-6" />
        </a>
      </div>

      <div className="space-y-3">
        <div className="text-sm text-gray-400">
          <span className="text-gray-300">Tech Stack:</span> {project.techStack}
        </div>
        <div className="text-sm text-gray-400">
          <span className="text-gray-300">Target:</span> {project.category}
        </div>
        <p className="text-gray-300 text-sm">{project.business}</p>
        <div className="text-sm text-green-400">
          Projected Value: {project.valuation}
        </div>
      </div>
    </div>
  </div>
);

ProjectCard.propTypes = {
  project: PropTypes.shape({
    icon: PropTypes.oneOfType([PropTypes.element, PropTypes.string]).isRequired,
    title: PropTypes.string.isRequired,
    subtitle: PropTypes.string,
    link: PropTypes.string.isRequired,
    techStack: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.string), PropTypes.string]),
    category: PropTypes.string,
    business: PropTypes.string,
    valuation: PropTypes.string,
  }).isRequired,
};

const Projects = () => {
  return (
    <div
      className="min-h-screen bg-gradient-to-br from-gray-900 to-black p-8"
      style={{
        backgroundImage: `url(${textureB})`,
        backgroundSize: 'auto',
        backgroundRepeat: 'repeat',
      }}
    >

  <div className="text-center space-y-6" style={{ marginTop: '10rem' }}>


      <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="relative inline-block shadow-lg"
      style={{
        width: '200px',
        height: '200px',
        borderRadius: '50%',
        overflow: 'hidden',
        border: '4px solid rgba(23, 8, 185, 0.2)'
      }}
    >
        <img
          src={cultures} // Replace with the path to the uploaded image
          alt="Abstract Brain and Heart"
          className="object-cover w-full h-full"
        />
      </motion.div>
      
      <div className="items-center font-bold">
        <p className='text-lg text-blue-200 font-bold py-8' style={{fontFamily: 'monospace'}}>
          There is no such thing as fame.
        </p>
            
        <p className='text-lg text-red-400 my-24' style={{fontFamily: 'monospace', marginTop: '40rem'}}>
          The world is not just letting you be yourself.
        </p>

        <p className='text-lg text-yellow-300 my-24' style={{fontFamily: 'monospace', marginTop: '40rem'}}>The world needs you to be yourself.</p>
        <p className='text-lg text-green-300 my-24' style={{fontFamily: 'monospace', marginTop: '40rem'}}>And your self is nothing - no thing at all.</p>
        <p className='text-lg text-purple-300 my-24' style={{fontFamily: 'monospace', marginTop: '40rem'}}>Stay nothing. Stay insecure. Stay politically uncomfortble and you will have it all.</p>
      
      </div>
          
        <div className="flex justify-between items-center mb-12" style={{ marginTop: '40rem' }}>
          <a
            href="https://github.com/williamit2045"
            target="_blank"
            rel="noopener noreferrer"
            className="flex items-right space-x-2 text-gray-400 hover:text-white"
          >
            <Github className="h-6 w-6" />
            <span>View on GitHub</span>
          </a>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {projectData.map((project) => (
            <ProjectCard key={project.id} project={project} />
          ))}
        </div>
      </div>
    </div>
  );
};

export default Projects;
