import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { motion } from 'framer-motion';
import { Mail, Github, Linkedin, Twitter, BookOpen } from 'lucide-react';
import emailjs from '@emailjs/browser';
import textureA from '../assets/folk-pattern-black.webp';
import textureB from '../assets/double-bubble-dark.webp';
import textureC from '../assets/carbon_fibre.webp';
import abstractBrainHeart from '../assets/images/sand.webp';

const SOCIAL_LINKS = [
  { name: 'GitHub', url: 'https://github.com/williamit2045', icon: Github, description: 'Check out my open source projects and contributions.' },
  { name: 'LinkedIn', url: 'https://www.linkedin.com/in/jdunbaugh/', icon: Linkedin, description: 'Connect with me professionally and see my journey.' },
  { name: 'Twitter', url: 'https://twitter.com/jdunbaugh', icon: Twitter, description: 'Follow my thoughts and updates.' },
  { name: 'Medium', url: 'https://medium.com/@mathewjamie/the-search-for-consciousness-two-hard-problems-and-a-bunch-of-absurdities-6b516bf5178c', icon: BookOpen, description: 'Read about my journey and insights on Medium.' },
  { name: 'Blog: Moral Apex', url: 'https://moralapex.wordpress.com/', icon: BookOpen, description: 'Explore my blog focused on ethical discussions.' },
  { name: 'Blog: Transhumanities', url: 'https://transhumanities.wordpress.com/', icon: BookOpen, description: 'Thoughts on transhumanism and future tech.' },
  { name: 'Blog: Ascension of Being', url: 'https://ascensionofbeing.wordpress.com/', icon: BookOpen, description: 'Exploring the human experience and personal growth.' },
  { name: 'Blog: JDunbaugh', url: 'https://jdunbaugh.wordpress.com/', icon: BookOpen, description: 'My personal blog on a variety of topics.' },
];

const Contact = () => {
  const [formData, setFormData] = useState({ name: '', email: '', message: '' });
  const [status, setStatus] = useState('');

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setStatus('sending');
    try {
      await emailjs.send(
        'service_6qtvoms',
        'template_qh8il3o',
        {
          to_email: 'williamit2060@gmail.com',
          from_name: formData.name,
          from_email: formData.email,
          message: formData.message,
        },
        '8_OlTHxuidKtAWkDh'
      );
      setStatus('success');
      setFormData({ name: '', email: '', message: '' });
    } catch {
      setStatus('error');
    }
  };

  return (
    
    <div
    className="min-h-screen bg-black text-white py-12 px-4"
    style={{ backgroundImage: `url(${textureB})`, backgroundRepeat: 'repeat' }}
  >

    <div className="text-center space-y-6" style={{ marginTop: '9rem' }}>
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="relative inline-block shadow-lg"
        style={{
          width: '200px',
          height: '200px',
          borderRadius: '50%',
          overflow: 'hidden',
          border: '4px solid rgba(255, 255, 255, 0.2)'
        }}
      >
        <img
          src={abstractBrainHeart} // Replace with the path to the uploaded image
          alt="Abstract Brain and Heart"
          className="object-cover w-full h-full"
          
        />
      </motion.div>
        <h2 className="text-lg text-orange-300 hover:text-purple-400 font-bold text-center" style={{fontFamily: 'monospace', marginTop: '4rem', marginBottom: '24rem'}}>You can&apos;t get cooler than me, because I am no mere thing.</h2>
        <p className="text-lg text-yellow-500 hover:text-blue-400 font-bold text-center py-72" style={{fontFamily: 'monospace'}}>And you can&apos;t get cooler than nothing.</p>
        <div className="text-center" style={{fontFamily: 'monospace', margin: '0rem', marginTop: '24rem', marginBottom: '28rem', padding: '0rem' }}>
          <p className="text-lg text-orange-100 hover:text-purple-200 font-bold text-center my-24 py-12">I can destroy my body, slowly, with ciggarettes.</p>
          <p className="text-md text-orange-100 hover:text-orange-200 font-bold text-center py-24">I can destroy my body, quickly, with fentanyl.</p>
          <p className="text-md text-orange-100 hover:text-orange-200 font-bold text-center py-24">I cannot destroy nothing - my own self.</p>
          <p className="text-md text-orange-100 hover:text-orange-200 font-bold text-center py-24">What is suicide then, but how it is done?</p>
          <p className="text-md text-orange-100 hover:text-orange-200 font-bold text-center py-24">If they give you no other choice, who is responsible?</p>
        </div>
      </div>
    <div className="max-w-7xl mx-auto grid grid-cols-1 lg:grid-cols-2 gap-12" style={{ marginTop: '16rem' }}>

      {/* Left Column - Social Links */}
      <div className="space-y-8 md:text-center">
        <h1 className="text-blue-200 text-bold md:text-left">My Social Media:</h1>
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
          {SOCIAL_LINKS.map(({ name, url, icon: Icon, description }) => (
            <a
              key={name}
              href={url}
              target="_blank"
              rel="noopener noreferrer"
              className="bg-gray-800 p-4 rounded-lg flex items-start space-x-4 hover:bg-gray-700 transition-colors duration-300"
              style={{ backgroundImage: `url(${textureC})`, backgroundRepeat: 'repeat' }}
            >
              <Icon className="h-6 w-6 text-blue-400" />
              <div>
                <h3 className="text-lg font-semibold">{name}</h3>
                <p className="text-sm text-gray-400">{description}</p>
              </div>
            </a>
          ))}
        </div>
        <h1 className="text-sm text-center text-cyan-200 md:text-left">
          Everyone who thinks they cannot code, or do anything, is gaslit. STOP. 
        </h1>
        <h1 className="text-sm text-center text-orange-200 md:text-left">
          English is harder than javascript fools! The answers come when you need them.
        </h1>
        <h1 className="text-sm text-center text-orange-200 md:text-left"> 
          You have a duty to honor the truth, your truth,
        </h1>
        <p className="text-sm text-center text-cyan-200 md:text-left"> 
          and the truth of your peers, even when politically inconvenient.
        </p>
      </div>

      {/* Right Column - Contact Form */}
      <div
        className="bg-gray-800 p-8 rounded-lg shadow-lg space-y-6"
        style={{ backgroundImage: `url(${textureA})`, backgroundRepeat: 'repeat' }}
      >

        <p className="text-md text-orange-100 hover:text-yellow-100 font-bold text-center">
          If you destroy me, you will always be afraid of what they think.
        </p>
          
        <form onSubmit={handleSubmit} className="space-y-6">
          {['message', 'email'].map((field) => (
            <div key={field}>
              <label
                htmlFor={field}
                className="block text-sm font-medium text-gray-400 mb-2 capitalize"
              >
                {field}
              </label>
              {field !== 'message' ? (
                <input
                  type={field}
                  id={field}
                  name={field}
                  value={formData[field]}
                  onChange={handleChange}
                  required
                  className="w-full px-4 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                  style={{ backgroundImage: `url(${textureB})`, backgroundRepeat: 'repeat' }}/>
              ) : (
                <textarea
                  id={field}
                  name={field}
                  value={formData[field]}
                  onChange={handleChange}
                  placeholder='Be as you are'
                  required
                  rows={4}
                  className="w-full px-4 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                  style={{ backgroundImage: `url(${textureB})`, backgroundRepeat: 'repeat' }}
                />
              )}
            </div>

          ))}
          <button 
  type="submit" 
  disabled={status === 'sending'} 
  className="w-full bg-blue-900 hover:bg-yellow-700 hover:text-black text-bold px-6 py-3 my-6 transition-colors"
> 
  <span className="text-lg text-orange-100 hover:text-blue-100 font-bold text-center whitespace-pre-line" style={{ fontFamily: 'monospace'}}>
    {status === 'sending' 
      ? 'Sending...' 
      : 'It is only because I am nothing that I am capable of anything and worth more than anything.\n\n Only those who know they are nothing\nAre worthy to be my judges and companions.'}
  </span>
</button>
    <p className="text-md text-orange-100 hover:text-blue-200 font-bold text-center">If you think you are safe living any other way, why? I would love to know how it is by anything but the temporary moral failure of your peers.</p>
          {status === 'success' && <p className="text-green-400 text-center mt-4">Message sent successfully!</p>}
          {status === 'error' && <p className="text-red-400 text-center mt-4">Failed to send message. Please try again.</p>}

          <div className="bg-transparent p-2 shadow-md text-blue-300 mb-2">
          <div className="flex flex-col justify-between items-start mb-12" style={{ fontFamily: 'monospace'}}>
            <h2 className="text-left mb-4">In case you need to know:</h2>
              <p className="mb-3 py-2">I was born as Jamie Blue Fichera to Donna Christine Fichera on May 15th, 1984, in Santa Cruz, California.</p>
              <p className="mb-3 py-2">In 1990, I was adopted by Michael and Linda Dunbaugh, and my legal name became Mathew Allen Dunbaugh.</p>
              <p className="mb-3 py-2">Throughout my early life, I continued to go by my birth name. My High School Diploma lists me as Jamie Dunbaugh.</p>
              <p className="mb-3 py-2">My DD214 from my Honorable Navy discharge names me as Mathew JamieAllen Dunbaugh.</p>
              <p className="mb-3 py-2">My Bachelor of Arts from the University of California, San Diego (UCSD) reads Mathew Jamie Dunbaugh.</p>
              <p className="mb-3 py-2 hover:text-blue-200">After careful reflection and research, I have chosen the name William Teach  a name I can earn for myself and grow into.</p>
            
          </div>
        </div>
        </form>
      </div>
    </div>
  </div>
  );
};

Contact.propTypes = {
  SOCIAL_LINKS: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      url: PropTypes.string.isRequired,
      icon: PropTypes.elementType.isRequired,
      description: PropTypes.string.isRequired,
    })
  ),
};

export default Contact;